import { notFound } from "next/navigation";
import { getHospitalById } from "../../../src/lib/data/hospitals";
import HospitalDetailClient from "./HospitalDetailClient";

export async function generateMetadata({ params }) {
  const { id } = await params;
  const hospital = id ? await getHospitalById(id) : null;
  if (!hospital) return {};
  return {
    title: hospital.name,
    description:
      hospital.description || "Explore this HEALO partner hospital in Korea.",
  };
}

export default async function HospitalDetailPage({ params }) {
  const { id } = await params;
  const hospital = id ? await getHospitalById(id) : null;
  if (!hospital) notFound();
  return <HospitalDetailClient id={id} />;
}
